<template>
  <div class="mulo-select-row">
    <!--  -->
    <list-default
      ref="list_default"
      :params="moreParams"
      :render-list="renderData"
      :api="api"
      :rows="rowsData"
      :filter="filter"
      :params-update-reload="paramsUpdateReload"
    >
      <!--  -->
      <template v-slot:item="{item,index}">
        <td class="check-row">
          <input
            type="checkbox"
            @change="itemChange(item,$event)"
            v-model="item.selectRows_isSelected"
          />
        </td>
        <slot name="item" :item="item" :index="index">
          <td></td>
        </slot>
      </template>
    </list-default>

    <div class="footer-btns">
      <div class="operation">
        <input v-model="onlyShowSelected" type="checkbox" />
        <label @click="onlyShowSelected=!onlyShowSelected"></label>
      </div>
      <button @click="cancel" class="btn btn-danger"></button>
      <button @click="selectSuccess" class="btn btn-success"></button>
    </div>

    <!--  -->
  </div>
</template>

<script>
import ListDefault from "./Default";
import { ListDefualtMixin } from "./../../mixins/ListMixin";

export default {
  name: "select-rows",
  mixins: [ListDefualtMixin],
  components: {
    "list-default": ListDefault
  },
  computed: {
    rowsData() {
      let before = [""];
      return before.concat(this.rows);
    }
  },
  watch: {
    selecteds(newValue, oldValue) {
      console.log("watch selecteds");
      this.selectedsData = this.selecteds;
    },
    onlyShowSelected(newValue, oldValue) {
      this.reLoadList(true);
    }
  },

  props: {
    // 
    paramsUpdateReload: {
      type: Boolean,
      default: false
    },

    //  * ,
    refreshTag: {
      type: Number
    },

    //  
    selecteds: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 
    listIdKey: {
      type: String,
      default: "id"
    }
  },
  created() {
    this.selectedsData = this.selecteds;
  },
  data() {
    return {
      // = this.selecteds
      selectedsData: [],
      // 
      onlyShowSelected: false,
      // 
      moreParams: {
        onlyShowSelected: false
      }
    };
  },
  methods: {
    //
    renderData(list) {
      //
      list.forEach((li, index) => {
        list[index].selectRows_isSelected = false;
        this.selectedsData.forEach((selectId, index2) => {
          if (selectId == li[this.listIdKey]) {
            list[index].selectRows_isSelected = true;
          }
        });
      });
      console.log(list, this.selectedsData);
      return list;
    },
    selectSuccess() {
      // ids
      // this.$nextTick(() => {
      //   this.$refs.list_default.loadLostData(true, {}, () => {});
      // });
      // 
      this.$emit("success", {
        selecteds: this.selectedsData
      });
    },
    //
    cancel() {
      this.$emit("cancel", {
        selecteds: this.selectedsData
      });
    },

    /**
     * ,
     *
     */
    itemChange(item, e) {
      console.log(item, e);
      //
      let idKey = item[this.listIdKey];
      let inArrIndex = this.inArray(idKey, this.selectedsData);
      if (inArrIndex !== false) {
        //
        this.selectedsData.splice(inArrIndex, 1);
      } else {
        this.selectedsData.push(idKey);
      }
    },
    /**
     * 
     */
    reLoadList(reset) {
      //
      this.moreParams = {
        onlyShowSelected: this.onlyShowSelected ? 1 : 0,
        selecteds: this.selectedsData
      };
      //
      this.$nextTick(() => {
        this.$refs.list_default.loadLostData(reset);
      });
    },

    /**
     * id
     *
     * @return index | false
     */
    inArray(serach, arr) {
      for (let index = 0; index < arr.length; index++) {
        const element = arr[index];
        if (serach == element) {
          return index;
        }
      }
      return false;
    }
  }
};
</script>

<style lang="scss" >
.mulo-select-row {
  .footer-btns {
    text-align: right;
  }
}
</style>