<template>
  <div class="mulo-filters">
    <!--  -->
    <div class="filter-rows">
      <!--  -->
      <div v-if="rulesData.search.visible" class="item">
        <div v-if="rulesData.search.title" class="field-name">{{rulesData.search.title}}</div>
        <el-input
          v-model="rulesModel['search']"
          class="search-row"
          :placeholder="rulesData.search.placeholder"
        ></el-input>
      </div>

      <!--  -->
      <template v-for="(li,index) in rulesData.filters">
        <div
          :key="li.field+''+index"
          :class="`item ${li.className?li.className:''} ${rulesData.joinClass?li.field:''}`"
        >
          <div v-if="li.title" class="field-name">{{li.title}}</div>
          <!--  -->
          <template v-if="li.type=='input'">
            <el-input
              v-model="rulesModel[ li.field ]"
              class="search-row"
              :placeholder="li.attr.placeholder || ''+(li.title || li.field)"
            ></el-input>
          </template>

          <!--  -->
          <template v-if="li.type=='select'">
            <el-select v-model="rulesModel[ li.field ]" clearable :placeholder="`li.placeholder || ''`">
              <el-option
                v-for="item in li.selects"
                :key="`${li.field}${item.key}`"
                :label="item.value"
                :value="item.key"
              ></el-option>
            </el-select>
          </template>

          <!--  -->
          <template v-if="li.type=='datetimerange'">
            <el-date-picker
              v-model="rulesModel[ li.field ]"
              type="datetimerange"
              :picker-options="pickerOptions"
              :value-format="li.valueFormat || 'yyyy-MM-dd HH:mm:ss'"
              :format="li.format || 'yyyy-MM-dd HH:mm:ss'"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              align="right"
            ></el-date-picker>
          </template>

          <!--  -->
          <template v-if="li.type=='timerange'">
            <el-time-picker
              is-range
              v-model="rulesModel[ li.field ]"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              placeholder=""
              :value-format="li.valueFormat ||  'HH:mm:ss'"
              :picker-options="{
                  format: li.format || 'HH:mm:ss'
                }"
            ></el-time-picker>
          </template>

          <!--  -->
          <template v-if="li.type=='checkboxgroup' ">
            <checkbox-group v-model="rulesModel[ li.field ]" :options="li.options"></checkbox-group>
          </template>

          <!--  -->

          <template v-if="li.type=='numberrange' ">
            <number-range v-model="rulesModel[ li.field ]" :placeholders="li.placeholders"></number-range>
          </template>
        </div>
      </template>

      <!--  -->
      <div v-if="!(rulesData.filters && rulesData.filters.length)" class="item">
        <el-button @click="submit"></el-button>
      </div>
    </div>

    <!--  -->
    <div v-if="rulesData.filters && rulesData.filters.length" class="operation-row">
      <el-button @click="submit"></el-button>
      <el-button @click="reset"></el-button>
    </div>
  </div>
</template>

<script>
import {
  Input,
  Button,
  Select,
  Option,
  DatePicker,
  TimePicker,
  Checkbox
} from "element-ui";
import MuloCheckBoxGroup from "./../../components/checkbox-group/CheckBoxGroup.vue";
import NumberRange from "./filters/NumberRange";

import { getType } from "./../../utils/util";

console.log(MuloCheckBoxGroup);
export default {
  name: "mulo-filter-row",
  components: {
    [Input.name]: Input,
    [Button.name]: Button,
    [Select.name]: Select,
    [Option.name]: Option,
    [DatePicker.name]: DatePicker,
    [TimePicker.name]: TimePicker,
    [Checkbox.name]: Checkbox,
    "checkbox-group": MuloCheckBoxGroup,
    [NumberRange.name]: NumberRange
  },
  computed: {
    //
    visibleSearchBtn() {
      return true;
    }
  },
  props: {
    rules: {
      default: () => {
        return {
          search: true,

          // 
          filters: []
        };
      }
    }
  },
  watch: {
    rules: {
      deep: true,
      handler(newValue, oldValue) {
        this.parseFilterData(this.rules);
      }
    }
  },
  created() {
    //rules
    this.parseFilterData(this.rules);
  },
  data() {
    return {
      selectList: [],

      //
      rulesModel: {
        abd: 123,
        //
        statusselect: []
      },
      rulesData: {
        // 
        search: {
          visible: true,
          field: "search",
          title: "",
          placeholder: ""
        },

        // 
        oprationRow: {
          //
          type: "search"
        },

        filters: [],

        //
        filtersDemo: [
          // 
          {
            type: "input",
            attr: {
              type: "number"
            },
            field: "name",
            title: ""
          },
          // select
          {
            type: "select",
            field: "status",
            title: "",
            selects: [
              { key: -1, value: "" },
              { key: 0, value: "" },
              { key: 1, value: "" }
            ]
          },

          // 
          {
            type: "datetimerange",
            field: "addtime",
            title: ""
          },

          //
          {
            type: "timerange",
            field: "jobtime",
            title: ""
          },

          // checkboxgroup
          {
            type: "checkboxgroup",
            field: "statusselect",
            title: ""
            // options: [
            //   { value: "1" },
            //   { value: "2" },
            //   { value: "3" },
            //   { value: "4" },
            //   { value: "5" },
            //   { value: "6" },
            //   { value: "7" }
            // ]
          },

          // 
          {
            type: "numberrange",
            field: "numberrange",
            title: "",
            placeholders: ["", ""]
          }
        ],

        //
        checkItems: [],

        //
        export: {
          //
          visible: true,
          fields: [
            { field: "title", title: "" },
            { field: "addtime", title: "" }
          ],
          //
          canSelect: false
        },

        //css,
        joinClass: false,
        class: ""
      },

      // 
      rulesModel: {
        search: ""
      },
      // 
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      resetStatus: false
    };
  },
  methods: {
    // 
    submit() {
      console.log(this.rulesModel);
      this.$emit("filter", {
        data: this.rulesModel
      });
    },
    //
    reset() {
      //
      this.resetStatus = true;

      this.rulesModel = {};
      this.$nextTick(() => {
        this.resetStatus = false;
      });
      this.$emit("filter", {
        data: this.rulesModel
      });
    },
    //
    parseFilterData(rules) {
      rules =rules || {}
      //
      if (typeof rules.search == "string") {
        this.rulesData.search.visible = true;
        this.rulesData.search.placeholder = rules.search;
      } else if (typeof rules.search == "boolean") {
        this.rulesData.search.visible = rules.search;
      } else if (typeof rules.search == "object") {
        this.rulesData.search = rules.search;
      }

      //
      if (rules.filters) {
        // 
        for (let index = 0; index < rules.filters.length; index++) {
          const element = rules.filters[index];
          if (!element.attr) {
            rules.filters[index].attr = {};
          }
        }
        this.rulesData.filters = rules.filters;
      }
    }
  }
};
</script>

<style lang="scss" >
$spaceDefault: 10px;

.mulo-filters {
  .filter-rows {
    padding: $spaceDefault 0;
    display: flex;
    flex-wrap: wrap;
    .item {
      padding: $spaceDefault;
      padding-left: 0;
      display: flex;
      align-items: center;
      .field-name {
        padding-right: $spaceDefault/2;
        word-break: keep-all;
        white-space: nowrap;
        overflow: hidden;
        min-width: 69px;
      }
      &.inline {
        min-width: 100%;
      }
    }
  }
  .search-row {
    width: 150px;
  }
}
</style>