<template>
  <div class="mulo-list-default">
    <!--  -->
    <slot name="filter-before"></slot>
    <slot name="filter">
      <div class="filter">
        <!--  -->
        <mulo-filter-row @filter="onFilter" :rules="filter"></mulo-filter-row>
      </div>
    </slot>
    <slot name="filter-after"></slot>
    <!--  -->
    <slot name="table" :vmdata="$data">
      <table class="table">
        <slot name="thead" :rows="rows">
          <thead>
            <tr>
              <!-- #  -->
              <slot name="rows" :rows="rows">
                <template v-for="(li,index) in rows">
                  <slot name="rows-item" :index="index">
                    <td :key="index">{{li}}</td>
                  </slot>
                </template>
              </slot>
            </tr>
          </thead>
        </slot>
        <!--  -->
        <slot name="tbody" :vmdata="$data">
          <tbody>
            <!-- #  -->
            <tr v-for="(li,index) in list" :key="index">
              <slot name="item" :item="li" :index="index">
                <td>{{index}}</td>
              </slot>
            </tr>
          </tbody>
        </slot>
      </table>
    </slot>

    <!--  -->
    <slot name="tfooter">
      <div class="table-footer">
        <div>
          <!--  -->
          <pagination
            @current-change="currentChange"
            :total="total"
            :page-size="psize"
            :current-page="page"
            @size-change="sizeChange"
            layout="total, sizes, prev, pager, next, jumper"
          ></pagination>
        </div>
        <slot name="footer-right">
          <div class="footer-right"></div>
        </slot>
      </div>
    </slot>
  </div>
</template>

<script>
// 
// import Pager from "./../pagination/Pager";
import { Pagination } from "element-ui";
// 
import FilterRow from "./FilterRow";
// 
import RulesSetting from "./../RulesSetting";
const rulesConfig = RulesSetting.getRules();

import { ListDefualtMixin } from "./../../mixins/ListMixin";

export default {
  name: "list-default",
  mixins: [ListDefualtMixin],
  components: {
    pagination: Pagination,
    [FilterRow.name]: FilterRow
  },
  props: {},
  data() {
    return {
      key: "",
      // 
      page: 1,
      total: 10,
      psize: 10,
      // 
      list: [],
      //
      moreParams: {}
    };
  },
  created() {
    this.getList();
  },
  watch: {
    params: {
      handler(newValue, oldValue) {
        if (this.paramsUpdateReload) {
          this.getList(true);
        }
      },
      deep: true
    }
  },
  methods: {
    isCheckEd(item) {},
    /**
     * 
     *
     * @props api  | +refs
     *
     * @param {Boolean} reset ()
     *
     */
    getList(reset) {
      //api 
      if (reset) {
        this.page = 1;
      }
      if (!this.api) {
        this.$emit("get-list", this.page);
        return;
      }
      this.loadLostData();
    },
    /**
     * 
     *
     * @param {Object} params 
     *
     * @example this.$refs.xxx.loadLostData(true)
     */
    loadLostData(reset, callback) {
      let params = {
        page: this.page,
        psize: this.psize,
        // 
        ...this.params,
        //
        ...this.moreParams
      };
      //get
      if (this.method == "get") {
        params = {
          params: params
        };
      }
      this.$http.post(this.api, params).then(res => {
        if (res.code != 200) {
          this.$toast(res.msg);
          return;
        }
        //
        let list = res.data.list;
        //
        list = this.renderList(list);
        //
        this.list = list;
        this.total = res.data.total;
      });
    },
    name() {},
    //
    rowsName() {},
    /**
     * psize
     *
     * @todo 
     */
    sizeChange(size) {
      this.$emit("size-change", size);
      this.psize = size;
      this.getList();
    },
    /**
     * 
     *
     * @todo 
     */
    currentChange(page) {
      page = Math.max(1, page);
      console.log(page);
      this.page = page;
      this.getList();
    },
    /**
     * 
     *
     */
    onFilter(res) {
      let filters = res.data;
      this.moreParams[rulesConfig.filters.paramName] = filters;
      this.getList(true);
    }
  }
};
</script>

<style lang="scss" scoped>
//
.mulo-list-page {
  .item {
  }
}
.table {
  width: 100%;
  thead {
    tr {
      td {
        // color: #4e4e4e;
      }
    }
  }
  tbody {
    tr {
      td {
      }
    }
  }
}
</style>